//----------------------------------------------------------//
//	SHARE BUTTONS - Floating Social Share Widget
//----------------------------------------------------------//

.share-buttons {
	display: flex;
	flex-direction: column; //      
	gap: 4px; // Bootstrap gap     
	z-index: 9999;
	margin: 10px;
	position: relative; //    
	
	//    
	&.right-widget {
		flex-direction: row-reverse; //   
		
		.widget-social {
			left: auto; //  left
			right: 0; //    
		}
	}
	
	//    
	&.top-widget {
		flex-direction: column-reverse; //   
	}
	
	// :   
	&.top-widget.right-widget {
		flex-direction: column-reverse; //    (  row-reverse)
		align-items: flex-end; //    
	}
	
	.share-button-main {
		z-index: 2;
		cursor: pointer;
		transform: rotate(0deg);
		flex-shrink: 0;
		position: relative; //     
		
		i {
			transition: transform 0.6s ease-in-out;
		}
		
		&.no-rotate {
			//     button
			transform: none !important;
			
			&.rotate,
			&.rotate-reverse {
				animation: none !important;
				transform: none !important;
			}
		}
		
		&.rotate {
			animation: rotate180 0.6s ease-in-out forwards;
		}
		
		&.rotate-reverse {
			animation: rotate180reverse 0.6s ease-in-out forwards;
		}
	}
	
	@keyframes rotate180 {
		from {
			transform: rotate(0deg);
		}
		to {
			transform: rotate(180deg);
		}
	}
	
	@keyframes rotate180reverse {
		from {
			transform: rotate(180deg);
		}
		to {
			transform: rotate(0deg);
		}
	}
	
	.widget-social {
		z-index: 1;
		flex-shrink: 0;
		position: absolute;
		bottom: 0;
		left: 0; //      
		width: 100%; //     
		transition: opacity 0.4s cubic-bezier(0.4, 0, 0.2, 1), transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
		opacity: 0;
		pointer-events: none;
		transform: translateY(0);
		
		i {
			position: relative;
			transition: color 0.3s ease;
		}
	}
	
	//    
	&.open {
		.widget-social {
			opacity: 1;
			pointer-events: auto;
		}
		
		//  1: Button  + Button 
		//   Button (51.6px) +  Button (51.6px) + gap (4px) = 55.6px 
		&.widget-main-button-elements-button .button-element {
			//  :    (51.6px) + gap (4px) = 55.6px
			&:first-of-type,
			&:nth-child(2) {
				transform: translateY(-55.6px) !important;
				transition-delay: 0.05s;
			}
			
			//  :  (55.6px) +   (51.6px) + gap (4px) = 111.2px
			&:nth-of-type(2),
			&:nth-child(3) {
				transform: translateY(-111.2px) !important;
				transition-delay: 0.1s;
			}
			
			//  : 111.2 + 55.6 = 166.8px
			&:nth-of-type(3),
			&:nth-child(4) {
				transform: translateY(-166.8px) !important;
				transition-delay: 0.15s;
			}
			
			//  : 166.8 + 55.6 = 222.4px
			&:nth-of-type(4),
			&:nth-child(5) {
				transform: translateY(-222.4px) !important;
				transition-delay: 0.2s;
			}
			
			//  : 222.4 + 55.6 = 278px
			&:nth-of-type(5),
			&:nth-child(6) {
				transform: translateY(-278px) !important;
				transition-delay: 0.25s;
			}
			
			//  : 278 + 55.6 = 333.6px
			&:nth-of-type(6),
			&:nth-child(7) {
				transform: translateY(-333.6px) !important;
				transition-delay: 0.3s;
			}
			
			//  : 333.6 + 55.6 = 389.2px
			&:nth-of-type(7),
			&:nth-child(8) {
				transform: translateY(-389.2px) !important;
				transition-delay: 0.35s;
			}
			
			//  : 389.2 + 55.6 = 444.8px
			&:nth-of-type(8),
			&:nth-child(9) {
				transform: translateY(-444.8px) !important;
				transition-delay: 0.4s;
			}
			
			//  : 444.8 + 55.6 = 500.4px
			&:nth-of-type(9),
			&:nth-child(10) {
				transform: translateY(-500.4px) !important;
				transition-delay: 0.45s;
			}
			
			//  : 500.4 + 55.6 = 556px
			&:nth-of-type(10),
			&:nth-child(11) {
				transform: translateY(-556px) !important;
				transition-delay: 0.5s;
			}
		}
		
		//  2: Button  + Icon 
		//   Button (51.6px) +  Icon (60px) + gap (4px)
		//  :    (51.6px) + gap (4px) = 55.6px
		//  :  =   (60px) + gap (4px) = 64px
		&.widget-main-button-elements-icon .icon-element {
			&:nth-child(2) {
				transform: translateY(-55.6px) !important; // 51.6 + 4
				transition-delay: 0.05s;
			}
			
			&:nth-child(3) {
				transform: translateY(-119.6px) !important; // 55.6 + 60 + 4
				transition-delay: 0.1s;
			}
			
			&:nth-child(4) {
				transform: translateY(-183.6px) !important; // 119.6 + 64
				transition-delay: 0.15s;
			}
			
			&:nth-child(5) {
				transform: translateY(-247.6px) !important; // 183.6 + 64
				transition-delay: 0.2s;
			}
			
			&:nth-child(6) {
				transform: translateY(-311.6px) !important; // 247.6 + 64
				transition-delay: 0.25s;
			}
			
			&:nth-child(7) {
				transform: translateY(-375.6px) !important; // 311.6 + 64
				transition-delay: 0.3s;
			}
			
			&:nth-child(8) {
				transform: translateY(-439.6px) !important; // 375.6 + 64
				transition-delay: 0.35s;
			}
			
			&:nth-child(9) {
				transform: translateY(-503.6px) !important; // 439.6 + 64
				transition-delay: 0.4s;
			}
			
			&:nth-child(10) {
				transform: translateY(-567.6px) !important; // 503.6 + 64
				transition-delay: 0.45s;
			}
			
			&:nth-child(11) {
				transform: translateY(-631.6px) !important; // 567.6 + 64
				transition-delay: 0.5s;
			}
		}
		
		//  3: Icon  + Icon 
		//   Icon (60px) +  Icon (60px) + gap (4px) = 64px 
		&.widget-main-icon-elements-icon .icon-element {
			&:nth-child(2) {
				transform: translateY(-64px) !important; // 60 + 4
				transition-delay: 0.05s;
			}
			
			&:nth-child(3) {
				transform: translateY(-128px) !important; // 64 + 64
				transition-delay: 0.1s;
			}
			
			&:nth-child(4) {
				transform: translateY(-192px) !important; // 128 + 64
				transition-delay: 0.15s;
			}
			
			&:nth-child(5) {
				transform: translateY(-256px) !important; // 192 + 64
				transition-delay: 0.2s;
			}
			
			&:nth-child(6) {
				transform: translateY(-320px) !important; // 256 + 64
				transition-delay: 0.25s;
			}
			
			&:nth-child(7) {
				transform: translateY(-384px) !important; // 320 + 64
				transition-delay: 0.3s;
			}
			
			&:nth-child(8) {
				transform: translateY(-448px) !important; // 384 + 64
				transition-delay: 0.35s;
			}
			
			&:nth-child(9) {
				transform: translateY(-512px) !important; // 448 + 64
				transition-delay: 0.4s;
			}
			
			&:nth-child(10) {
				transform: translateY(-576px) !important; // 512 + 64
				transition-delay: 0.45s;
			}
			
			&:nth-child(11) {
				transform: translateY(-640px) !important; // 576 + 64
				transition-delay: 0.5s;
			}
		}
		
		//  4: Icon  + Button 
		//   Icon (60px) +  Button (51.6px) + gap (4px)
		//  :    (60px) + gap (4px) = 64px
		//  :  =   (51.6px) + gap (4px) = 55.6px
		&.widget-main-icon-elements-button .button-element {
			&:nth-child(2) {
				transform: translateY(-64px) !important; // 60 + 4
				transition-delay: 0.05s;
			}
			
			&:nth-child(3) {
				transform: translateY(-119.6px) !important; // 64 + 51.6 + 4
				transition-delay: 0.1s;
			}
			
			&:nth-child(4) {
				transform: translateY(-175.2px) !important; // 119.6 + 55.6
				transition-delay: 0.15s;
			}
			
			&:nth-child(5) {
				transform: translateY(-230.8px) !important; // 175.2 + 55.6
				transition-delay: 0.2s;
			}
			
			&:nth-child(6) {
				transform: translateY(-286.4px) !important; // 230.8 + 55.6
				transition-delay: 0.25s;
			}
			
			&:nth-child(7) {
				transform: translateY(-342px) !important; // 286.4 + 55.6
				transition-delay: 0.3s;
			}
			
			&:nth-child(8) {
				transform: translateY(-397.6px) !important; // 342 + 55.6
				transition-delay: 0.35s;
			}
			
			&:nth-child(9) {
				transform: translateY(-453.2px) !important; // 397.6 + 55.6
				transition-delay: 0.4s;
			}
			
			&:nth-child(10) {
				transform: translateY(-508.8px) !important; // 453.2 + 55.6
				transition-delay: 0.45s;
			}
			
			&:nth-child(11) {
				transform: translateY(-564.4px) !important; // 508.8 + 55.6
				transition-delay: 0.5s;
			}
		}
	}
	
	//  
	&.closing {
		.widget-social {
			opacity: 0;
			pointer-events: none;
			transform: translateY(0);
		}
		
		//     
		.widget-social:nth-of-type(10) {
			transition-delay: 0.05s;
		}
		
		.widget-social:nth-of-type(9) {
			transition-delay: 0.1s;
		}
		
		.widget-social:nth-of-type(8) {
			transition-delay: 0.15s;
		}
		
		.widget-social:nth-of-type(7) {
			transition-delay: 0.2s;
		}
		
		.widget-social:nth-of-type(6) {
			transition-delay: 0.25s;
		}
		
		.widget-social:nth-of-type(5) {
			transition-delay: 0.3s;
		}
		
		.widget-social:nth-of-type(4) {
			transition-delay: 0.35s;
		}
		
		.widget-social:nth-of-type(3) {
			transition-delay: 0.4s;
		}
		
		.widget-social:nth-of-type(2) {
			transition-delay: 0.45s;
		}
		
		.widget-social:nth-of-type(1) {
			transition-delay: 0.5s;
		}
	}
	
}

//    
@media (max-width: 768px) {
	.share-buttons {
		margin: 5px;
		gap: 6px;
	}
}

